<template>
  <v-layout>
    <v-flex xs12 sm6 offset-sm3>
      <v-card>
        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0">To do:</h3>
          </div>
        </v-card-title>
          <v-card-text>
            <TodoItem
            v-for="(todo, index) in todos"
            :key="index"
            :todo="todo">
            </TodoItem>
              <v-form>
                <v-text-field ref="input"
                              v-model="newItem"
                              label="What do you need to do?"
                              @keydown.enter.prevent="addTodo"
                              autofocus>
                </v-text-field>
              </v-form>
            </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="addTodo">Save Item</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import TodoItem from './components/TodoItem'

export default {
  name: 'App',
  components: {
    TodoItem
  },
  data () {
    return {
      newItem: ''
    }
  },
  computed: {
    todos () {
      return this.$store.state.todos
    }
  },
  methods: {
    addTodo () {
      if (this.newItem) {
        this.$store.dispatch('addTodo', this.newItem)
        this.newItem = null
      }
    }
  }
}
</script>
